export async function getCose() {
    const response = await fetch('/api/cose')
    return await response.json();
}

export async function addCosa(cosa) {
    const response = await fetch(`/api/cosa`, {
        method: 'POST',
        headers: {'Content-Type': 'application/json'},
        body: JSON.stringify({cosa: cosa})
      })
    return await response.json();
}

export async function delCosa(i) {
    const response = await fetch(`/api/del/i`, {
        method: 'POST',
        headers: {'Content-Type': 'application/json'},
        body: JSON.stringify({i: i})
      })
    return await response.json();
}