import React, { Component } from 'react';
import styles from './styles/Home.module.css'
import Lista from './component/lista'
import Aggiungi from './component/aggiungi'
import { getCose, addCosa, delCosa } from './UserService'
import tachyons from 'tachyons'
import './styles/globals.css'


class App extends Component {

state = {
  cosa: "",
  cose: ["pane","acqua"],
  i: 0,
  svuota: "scrivi"
}

  componentDidMount() {
    this.getCose()
  }


  addCosa = (e) => {
    let cosa = this.state.cosa
    if(!(cosa === "")) {
    addCosa(this.state.cosa)
    .then(cose => {
      this.setState({cosa: ""})
      console.log(cose);
      this.setState({cose: cose})
    })
  }
  }

  delCosa = (e) => {
    let i = this.state.i;
    i = e.target.getAttribute('num');
    delCosa(i)
    .then(cose => {
      this.setState({cose: cose})
    })
  }

  getCose = () => {
    getCose()
    .then(cose => {
      console.log(cose);
      this.setState({cose: cose})
    })
  }

  onChangeForm = (e) => {
    let cosa = this.state.cosa;
      cosa = e.target.value;
      console.log(cosa);
      this.setState({cosa: cosa})
      console.log(this.state.cosa)
    }

  

  render () {
  return (
    <div className={styles.container}>
      <main className={styles.main}>
        <h1 className="f1">
          Lista della Spesa
        </h1>
        <Aggiungi
        onChangeForm={this.onChangeForm}
        addCosa={this.addCosa}
        svuota={this.state.svuota}/>
        <Lista cose={this.state.cose}
        delCosa={this.delCosa}/>
      </main>
    </div>
  )
}
}

export default App